import * as reducers from '../reducers/index';
import Immutable from 'immutable';
import sendMsg from '../../ws/sendMsg';
import { combineReducers } from 'redux-immutable';
import actionType from '../action/action-type';
let { FETCH_FAILURE, FETCH_SUCC, FETCH } = actionType;
import { createStore, applyMiddleware, compose } from 'redux';
/**
 *  action  state
 */
const logger = store => next => action => {
	console.group(action.type);
	console.info('dispatching', action);
	let result = next(action);
	let nextState = store.getState();
	console.log('next state', nextState.toJS ? nextState.toJS() : nextState);
	console.groupEnd(action.type);
	return result;
};

/**
 * 
 *
 */
const syncStateToSer = store => next => action => {
	let state = store.getState();
	let result = next(action);
	let nextState = store.getState();
	let test = {
		// ADD_BRANCH: 1,
		// SWITCH_BRANCH: 1,
		// SWITCH_GROUP: 1,
		// CHANGE_GROUP_NAME: 1,
		// CHANGE_BRANCH_NAME: 1,
		// TOGGLE_BRANCH_DIS: 1,
		// TOGGLE_GROUP_DIS: 1,
		// TOGGLE_RULE_DIS: 1,
		TOGGLE_FLOD: 1,
	};
	if (test[action.type]) {
		if (!state.get('hosts').equals(nextState.get('hosts'))) {
			sendMsg.updateRule(nextState.get('hosts').toJS());
		}
	}
	return result;
};

/**
 *  state  listener 
 */
const crashReporter = store => next => action => {
	try {
		return next(action);
	} catch (err) {
		console.error('Caught an exception!', err);
		console.error('action', action);
		console.error('state', store.getState());
		throw err;
	}
};

/**
 *  action  promise
 *  promise  resolved action 
 *  promise  `dispatch`  rejection
 */
const vanillaPromise = store => next => action => {
	if (typeof action.then !== 'function') {
		return next(action);
	}
	return Promise.resolve(action).then(store.dispatch);
};

/**
 *  { promise }  action
 *
 *  middleware  action `promise` resolve  action
 *
 * `dispatch`  promise 
 */
const readyStatePromise = store => next => action => {
	if (!action.promise) {
		return next(action);
	}
	function makeAction(type, data) {
		let newAction = Object.assign({}, action, { type }, data);
		delete newAction.promise;
		return newAction;
	}
	next(makeAction(FETCH));
	return action.promise.then(result => next(makeAction(FETCH_SUCC, { result })), error => next(makeAction(FETCH_FAILURE, { error })));
};

/**
 *  action
 *  `dispatch`  `getState` 
 *
 *  `getState()`  `dispatch()` 
 *
 * `dispatch` 
 */
const thunk = store => next => action => (typeof action === 'function' ? action(store.dispatch, store.getState) : next(action));
const initialState = new Immutable.fromJS({
	// 
	hosts: [],
	// 
	fetchRule: {},
	// 
	drawerStatus: true,
	// 
	selectRule: {},
	disCache: false,
	cacheFlush: false,
	// url
	remoteUpdateRuleUrl: '',
});
// reducers
let toDo = combineReducers(reducers);

//  stroe
let middleware = [thunk, vanillaPromise, readyStatePromise, syncStateToSer, crashReporter];
if (window.config.env === 'dev') {
	middleware.push(logger);
}
let store = createStore(
	toDo,
	initialState,
	compose(
		applyMiddleware(...middleware),
		window.devToolsExtension ? window.devToolsExtension() : f => f
	)
);
export default store;



