let result;
/**  action action
{
	// action
	type: navType,
	// action
	payload: result,
}
*/
export const {
	/**
	 *
	 *@param boolean 
	 */
	disCache,
	/**
	 *
	 *@param boolean 
	 */

	monitorStatus,
	/**
	 *
	 *@param boolean 
	 */

	monitorFilterStatus,
	/**
	 *  meta contentTypetext/html
	 */
	cacheFlush,
} = (result = createActions(...Object.keys(navType)));
export default result;



