// 
// 
/*
 *
 *  Object
 *
 * {
 * 	path: "typepath"
 * 	param: ""
 * }
 */
import { send } from './ws';

// config
export let fetchConfig = () => {
	return send('fetchConfig')();
};

// rule
export let updateRule = data => {
	if (data && data.length >= 0) {
		let result = {
			param: {
				rules: data,
			},
			path: 'rule',
		};
		return send('saveConfig')(result);
	}
};

// 
export let remoteUpdateRule = url => {
	if (url) {
		let result = {
			param: {
				url: url,
			},
			path: 'remoteUpdateRule',
		};
		return send('remoteUpdateRule')(result);
	}
};

// 
export let disCache = status => {
	status = !!status;
	let result = {
		param: {
			status: status,
		},
		path: 'disCache',
	};
	return send('saveConfig')(result);
};

export let cacheFlush = status => {
	status = !!status;
	let result = {
		param: {
			status: status,
		},
		path: 'cacheFlush',
	};
	return send('saveConfig')(result);
};

// 
export let monitorStatus = status => {
	status = !!status;
	let result = {
		param: {
			monitorStatus: status,
		},
		path: 'monitor',
	};
	return send('saveConfig')(result);
};

// 
export let monitorFilterStatus = status => {
	status = !!status;
	let result = {
		param: {
			monitorFilterStatus: status,
		},
		path: 'monitor',
	};
	return send('saveConfig')(result);
};

// 
export let monitorFilterType = type => {
	status = !!status;
	let result = {
		param: {
			monitorFilterType: type,
		},
		path: 'monitor',
	};
	return send('saveConfig')(result);
};

//  dataUrl
export let hiddenDataUrl = status => {
	status = !!status;
	let result = {
		param: {
			hiddenDataUrl: status,
		},
		path: 'monitor',
	};
	return send('saveConfig')(result);
};

export let getConDetail = param => {
	let result = {
		param,
		path: 'getConDetail',
	};
	return send('getConDetail')(result);
};

export default {
	fetchConfig,
	updateRule,
	disCache,
	remoteUpdateRule,
	monitorStatus,
	monitorFilterStatus,
	monitorFilterType,
	hiddenDataUrl,
	cacheFlush,
};



